@import "../mixins.scss";

.name-form {
	@include column-center;
	width: 50%;
	margin: 5%;
}

.name-input {
	width: 250px;
	padding: 3%;
	margin: 2.5%;
	outline: none;
	box-shadow: inset 0 0 11px black;
	font-size: 16px;
}

.name-btn {
	@include btn;
	@include btn-anchor;
}

.disabled-name-btn {
	@include btn;
	@include btn-anchor;
	opacity: 0.5;
	cursor: auto;
	&:hover {
		transform: none;
	}
}

@media only screen and (max-width: 500px) {
	.name-form {
		width: 90%;
	}
	.disabled-name-btn,
	.name-btn {
		width: 80%;
	}

	.name-input {
		width: 75%;
	}
}
