@import "./variables.scss";

@mixin column-center {
	display: flex;
	flex-direction: column;
	align-items: center;
}

@mixin column {
	display: flex;
	flex-direction: column;
}

@mixin row-wrap-around {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;
}

@mixin btn-anchor {
	margin: 5%;
	width: 300px;
}

@mixin btn {
	background-color: $primary-gold;
	border: 2px solid $primary-maroon;
	border-radius: 20px;
	font-family: $primary-font;
	cursor: pointer;
	outline: none;
	box-shadow: 5px 5px 5px black;
	font-size: 1.25em;
	width: 100%;
	padding: 2%;
	&:hover {
		transform: scale(1.05);
		transition-duration: 0.4s;
	}
}

@mixin result-page {
	@include column-center;
	height: 100%;
}
